"use server";

import {
    StudioProfile,
    getAllStudios,
    getStudioById,
    saveStudio,
    createEmptyStudio,
    deleteStudio
} from "@/lib/db-studio";

// Get All
export async function fetchStudios() {
    return getAllStudios();
}

// Get One
export async function fetchStudio(id: string) {
    return getStudioById(id);
}

// Upsert
export async function updateStudio(data: StudioProfile) {
    try {
        saveStudio(data);
        return { success: true, message: "Studio saved successfully." };
    } catch (e: any) {
        return { success: false, message: e.message };
    }
}

// Create New
export async function createNewStudioAction() {
    const newStudio = createEmptyStudio();
    saveStudio(newStudio);
    return newStudio.id;
}

// Delete
export async function deleteStudioAction(id: string) {
    deleteStudio(id);
    return { success: true };
}
